import React from "react";
import _ from 'lodash'
import {
    Form,
    Button,
    Card
} from 'react-bootstrap'
import LogoCastrol from '../assets/images/r0l90w.jpg'


class LoginPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            username: null,
            password: null,
            userList: [{
                user: 'admin',
                pass: 'qwerty',
                permission: 'super-admin'
            }, {
                user: 'user',
                pass: 'castrol',
                permission: 'admin'
            }]

        }
    }

    handleLogin = () => {
        const {username, password, userList} = this.state
        _.forEach(userList, adm => {
            if (adm['user'] === username && adm['pass'] === password) {
                this.props.history.push('/admin/admin-page')
                const userInfo = {
                    name: adm['user'].toUpperCase(),
                    permission: adm['permission']
                }
                sessionStorage.setItem('userInfo', JSON.stringify(userInfo))
            }
        })
    }

    render() {
        return (
            <div className={'container'}>
                <div className={'row justify-content-center'}>
                    <div className={'col-md-5 col-12 mt-5'}>
                        <div className={'col-md-12 col-12 col-lg-12 col-sm-12 row justify-content-center pr-0'}>
                            <img variant="top" width="300px"  src={LogoCastrol}/>
                        </div>
                        <Card>
                            <Card.Body>
                                <Card.Title className={'row justify-content-center'}>login</Card.Title>
                                <Card.Text>
                                    <Form>
                                        <Form.Group controlId="formBasicEmail">
                                            <Form.Label>User</Form.Label>
                                            <Form.Control
                                                onChange={e => this.setState({username: e.target.value})}
                                                autoComplete={'off'}
                                                type="text" placeholder="User"/>
                                        </Form.Group>

                                        <Form.Group controlId="formBasicPassword">
                                            <Form.Label>Password</Form.Label>
                                            <Form.Control
                                                onChange={e => this.setState({password: e.target.value})}
                                                autoComplete={'off'}
                                                type="password" placeholder="Password"/>
                                        </Form.Group>
                                        <Button style={{width:"100%"}}
                                            onClick={e => {
                                                this.handleLogin()
                                            }}
                                            variant="success" type="submit">
                                            เข้าสู่ระบบ
                                        </Button>
                                    </Form>
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    </div>
                </div>
            </div>
        )
    }
}

export default LoginPage