    componentWillMount() {
        const todayDate = new Date()
        const date = moment(todayDate).format('YYYY-MM-DD')
        let {credentials} = this.state
        // credentials['startDate'] = moment(date).add(-7, 'day').format('YYYY-MM-DD')
        // credentials['endDate'] = moment(date).add(7, 'day').format('YYYY-MM-DD')
        credentials['startDate'] = '2020-10-12'
        credentials['endDate'] = '2021-01-12'
        credentials['status'] = 'ALL'
        credentials['search'] = ''
        // credentials['startDate'] = '2019-10-10'
        // credentials['endDate'] = '2021-10-01'
        this.setState({credentials})
    }

    componentDidMount() {
        this.handleShowLoading()
        this.fetchAppointmentData()
        this.fetchRegionData()
        let userInfo = sessionStorage.getItem('userInfo')
        userInfo = JSON.parse(userInfo)
        this.setState({
            userInfo: userInfo
        })
    }

    handleShowLoading = () => {
        this.setState({
            isShowLoading: !this.state.isShowLoading
        })
    }
    fetchRegionData = () => {
        let rowData = 0
        axios.get(`${process.env.REACT_APP_BACKEND_API}/add-to-store-pcr-get/[Sp_Web_BranchRegion]/${rowData}`).then(resp => {
            this.setState({
                regionData: resp.data
            })

        }).catch(e => {
            console.log(e.response)
        })
    }

    fetchAppointmentData = () => {

        let {credentials, isShowLoading} = this.state
        let creds = `0 , '${credentials['startDate']}' , '${credentials['endDate']}' , '${credentials['status']}' ,'${credentials['search']}'`
        axios.get(`${process.env.REACT_APP_BACKEND_API}/add-to-store-pcr-get/[Sp_Web_AllAppointment]/${creds}`).then(resp => {
            const res = []
            _.forEach(resp.data, val => {
                res.push(val)
            })
            this.setState({
                dataAppointments: res
            })
        }).catch(e => {
            console.log(e.response)
        }).finally(e => {
            this.handleShowLoading()
        })
    }

    handleDataAppointments = () => {
        return (<table className={'table border table-striped hover table-sm'} style={{fontSize: '0.7rem'}}>
            <thead>
            <tr>
                <th>#</th>
                {this.handleHeadTable()}
                <th>แก้ไข</th>
            </tr>
            </thead>
            <tbody>
            {this.handleDataTable()}
            </tbody>
        </table>)
    }

    handleHeadTable = () => {
        // AppDate: "2020-09-26T00:00:00.000Z"
        // AppID: 1006
        // AppTime: "08:30"
        // BranchID: 201
        // BranchName: " TMM"
        // BranchTelNo: "0946356277,0638526222"
        // BranchUrl: "https://www.google.com/maps/?q=16.592133,101.99789"
        // MemEmail: "tana@gmail.com"
        // MemName: "PEARL  THANYANAN"
        // MemTelno: "0987953547"
        // Note: ""
        // Package: "Castrol MAGNATEC SUV 5W-30"
        // SalesTelNo: "0638526222"
        // Status: "BOOK"
        const {dataAppointments, dataDictLag} = this.state
        const f_hide = ['AppID', 'BranchID', 'MemEmail', 'BranchUrl']
        const fix_width = ['AppDate', 'SalesTelNo']
        const styleWidth = {
            AppDate: '6%',
            SalesTelNo: '7%'
        }
        return _.map(dataAppointments[0], (eachVal, eachKey) => {
                return f_hide.includes(eachKey) ? null :
                    <th style={{width: `${fix_width.includes(eachKey) ? styleWidth[eachKey] : ''}`}}>{dataDictLag[eachKey]}</th>
            }
        )
    }

    handleDataTable = () => {
        const {dataAppointments} = this.state
        // console.log(this.checkPermission())
        return _.map(dataAppointments, (val, idx) => {
                return <tr
                    style={val['Status'] === 'REJECT' ? {backgroundColor: 'rgb(255 91 120 / 25%)'} : val['Status'] === 'CONFIRM' ?
                        {backgroundColor: 'rgb(241 225 79 / 25%)'} : val['Status'] === 'COMPLETE' ? {backgroundColor: 'rgb(49 178 78 / 25%)'} :
                            val['Status'] === 'REBOOK' ? {backgroundColor: 'rgb(255 160 122 / 42%)'} : null}>
                    <td>{idx + 1}</td>
                    {this.handleValueData(val)}
                    <td><i className={`far fa-edit fa-1x ${!this.checkPermission() ? 'disabled-thing' : ''}`}
                           style={{cursor: 'pointer'}}
                           onClick={e => this.handleShow(val)}/></td>
                </tr>
            }
        )
    }

    checkPermission = () => {
        const permission = this.state.userInfo?.permission || null
        const bool = permission === 'super-admin' ? true : false
        return bool
    }
    handleValueData = (val) => {
        const f_date = ['AppDate']
        const f_hide = ['AppID', 'BranchID', 'MemEmail', 'BranchUrl']
        const f_status = ['Status']
        const f_tooltip = ['Note']
        return _.map(val, (eachVal, key) => {
                // console.log(f_hide.includes(key))
                return f_date.includes(key) ? <td>{moment(eachVal).format('YYYY-MM-DD')}</td> :
                    f_tooltip.includes(key) ? <td
                            style={{
                                maxWidth: '12px', whiteSpace: 'nowrap',
                                overflow: 'hidden',
                                textOverflow: 'ellipsis'
                            }}
                            data-tip={eachVal}
                            width={'10%'}
                        >{eachVal}<ReactTooltip/>
                        </td> :
                        // f_status.includes(key) ? this.handleOption(eachVal, val) :
                        f_hide.includes(key) ? null : <td>{eachVal}</td>
            }
        )
    }
    handleOption = (eachVal) => {
        return <Form.Control defaultValue={eachVal}
                             size="sm" as="select"
                             onChange={e => this.handleChangeStatus(e.target.value)}

        >
            {/*<option value={eachVal} style={{display: 'none'}}>{eachVal}</option>*/}
            <option value={'BOOK'}>BOOK</option>
            <option value={'REBOOK'}>REBOOK</option>
            <option value={'CONFIRM'}>CONFIRM</option>
            <option value={'REJECT'}>REJECT</option>
            <option value={'COMPLETE'}>COMPLETE</option>
        </Form.Control>
    }

