import React from 'react'
import {Button} from "react-bootstrap"
import CastrolLogoSvg from '../assets/images/castrol.svg'
import headerBgBig from '../assets/images/header_bg_big.png'

class Topbar extends React.Component {
    constructor(props) {
        super(props);

    }

    clearSession = () => {
        // sessionStorage.removeItem('userInfo')
        // this.props.history.push('/admin/login-page')
    }

    render() {
        return (
            <div className={'row'}>
                <div className={'col-12 d-flex parent p-0'}>
                    {this.props.isAdminPage ?
                        <Button variant="light" size={'sm'} className={'btn-logout'}
                             onClick={e => this.props.clearSession()}>logout</Button> : null}
                    <img
                        alt=""
                        src={headerBgBig}
                        width="200"
                        height="55"
                        style={{zIndex: 3}}
                        className="d-inline-block align-top mr-3"
                    />
                    <img
                        alt=""
                        src={CastrolLogoSvg}
                        width="200"
                        height="30"
                        style={{zIndex: 3, position: 'absolute', left: '45px', top: '5px'}}
                        className="d-inline-block align-top mr-3 mt-2"
                    />
                    <div className={'nr-header__inner-wrap'}>.</div>
                </div>
            </div>
        );
    }
}

export default Topbar