import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './assets/custom.css'
import './App.css';
import Questionnaire from "./pages/Questionnaire";
import AdminPage from "./pages/AdminPage";
import Appointment from "./pages/Appointment";
import loginpage from "./pages/LoginPage";
import KobKhub from "./pages/KobKhub";
import 'animate.css/animate.min.css'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    useParams,
    withRouter
} from "react-router-dom";
import TestSql from './pages/TestSql'

// import { useParams } from "react-router"
import ReactPixel from 'react-facebook-pixel';

ReactPixel.init('330895248051041', {}, { debug: true, autoConfig: true });
// ReactPixel.pageView()
ReactPixel.fbq('track', 'All page')
function App() {
    return (
        <div>
            <Router>
                <Switch>
                    <Route path="/question" component={Questionnaire}/>
                    <Route path="/test-sql" component={TestSql}/>
                    <Route path="/admin/login-page" component={loginpage}/>
                    <Route path="/admin/admin-page" component={AdminPage}/>
                    <Route path="/Appointment/:id" component={Appointment}/>
                    <Route path="/thankyou" component={KobKhub}/>
                    {/*<Route path="/Appointment/:id/:name" component={Appointment}/>*/}
                    <Route path="/" component={Questionnaire}/>
                </Switch>
            </Router>
        </div>
    );
}

export default App;
