    componentDidMount() {
        this.fetchAppointmentData()
        this.fetchRegionData()
    }

    handleShowLoading = () => {
        this.setState({
            isShowLoading: !this.state.isShowLoading
        })
    }

    fetchRegionData = () => {
        let rowData = 0
        axios.get(`${process.env.REACT_APP_BACKEND_API}/add-to-store-pcr-get/[Sp_Web_BranchRegion]/${rowData}`).then(resp => {

            this.setState({
                regionData: resp.data,
            })

        }).catch(e => {
            console.log(e.response)
        })
    }

    fetchAppointmentData = () => {
        axios.get(`${process.env.REACT_APP_BACKEND_API}/add-to-store-pcr-get/[Sp_Web_AppointmentByID]/${this.id}`).then(resp => {
            let mapBrl = null
            let stateName = null
            const f_packageBDetail = ['AGED']
            if (resp.data.length > 0) {
                mapBrl = resp.data[0]['BranchUrl']
                stateName = f_packageBDetail.includes(resp.data[0]['MemType']) ? 'productB' : 'product'
            }
            this.setState({
                dataAppointments: resp.data,
                mapBrl: mapBrl,
                stateName: stateName
            })
        }).catch(e => {
            console.log(e.response)
        }).finally(e => {
            this.handleShowLoading()
        })
    }
    handleForloop = (number) => {
        var i
        let peopleToReturn = []
        for (i = 0; i < number; i++) {
            peopleToReturn.push(<small className={'col-12 p-1'}>
                <Card.Img
                    style={{width: '10px', height: '10px'}}
                    variant="top"
                    src={Star}/>
            </small>)
        }
        return peopleToReturn
    }


