    handlePattern = (iptType, isValidating = false) => {
        switch (iptType) {
            case 'text':
                // return isValidating ? '[^p{Latin}p{Thai}]' : '^[a-zA-Z ]*${Thai}]'
                return isValidating ? '(?:[^\\d\\W]+)|[^\\p{Latin}\\p{Thai}]+' : '(?:[^\\d\\W]+)|[^\\p{Latin}\\p{Thai}]+'
            case 'numbertel':
                // return '^[0-9]+$'
                return '^[08|02][0-9]+[0-9]{8}$'
            case 'email':
                return '[a-z0-9._%+-]+@[a-z0-9.-]+\\.[a-z]{2,}$'
            case 'time':
                return '^([0-1]?[0-9]|2[0-3]):[0-5][0-9]$'
            case 'date':
                return '([12]\\d{3}-(0[1-9]|1[0-2])-(0[1-9]|[12]\\d|3[01]))'
            default :
                return ''
        }
    }

