    handleChangeCustomerDetail = (val, key, idx) => {
        const {savedData, branchDetails} = this.state
        // debugger
        const unSave = new Set(['scheduleDate'])
        let strDate = new Date()
        strDate = moment(strDate).add(2, 'days').format('YYYY-MM-DD')
        // console.log(val)
        const endDate = '2021-01-12'
        if (!unSave.has(key) || ((unSave.has(key) && strDate <= val) && (unSave.has(key) && endDate >= val))) {
            if (key === 'scheduleDate') {
                branchDetails[idx]['customerDetail'][key]['isShowWarning'] = false
            }
            savedData[key] = val
            this.setState({
                savedData,
                branchDetails
            })
        } else {
            if (key === 'scheduleDate') {
                if (endDate < val) {
                    branchDetails[idx]['customerDetail'][key]['warningText'] = 'กรุณาเลือกวันที่ไม่เกิน 12 มกราคม 2564'
                } else if (strDate >= val) {
                    branchDetails[idx]['customerDetail'][key]['warningText'] = 'กรุณาเลือกวันจองล่วงหน้าก่อน 2 วัน'
                }
            }
            branchDetails[idx]['customerDetail'][key]['isShowWarning'] = true
            this.setState({branchDetails})
        }
    }

    stateMapping = () => {
        const state = this.state.activeState
        const obj = {
            customerDetail: 'customerDetails',
            branchDetail: 'branchDetails'
        }
        return obj[state]
    }

    handleSubmit = () => {
        const {validated, savedData} = this.state
        const stateName = this.stateMapping()
        let isValidated = true
        const res = savedData
        const data = this.state[stateName]
        _.forEach(data[0]['customerDetail'], (value, key) => {
            const match = this.handlePattern(value['subType'], true)
            const text = res[key]
            if (text === null || text.match(match) == null) {
                isValidated = false
            }
        })

        this.setState({
            validated: true
        })

        return isValidated === true

    }
    setShowLoading = () => {
        this.setState({
            isShowLoading: !this.state.isShowLoading
        })
    }
    handleSubmitQuestion = () => {
        const {questions} = this.state
        let isValidated = true
        let idx = null
        let choiceValidated = true
        _.forEach(questions, (val, qIdx) => {
            choiceValidated = true
            idx = qIdx
            const {answer, choices, isSkipValidated} = val
            // isSkipValidated
            if (!isSkipValidated) {
                _.forEach(choices, ch => {
                    // console.log(ch)
                    if (answer[ch['isSaveTo']] !== null) {
                        if (answer[ch['isSaveTo']] === ch.choice) {
                            if (ch.thens) {
                                _.forEach(ch.thens, thenl => {
                                    if (qIdx !== 3) {
                                        if (answer[thenl['isSaveTo']] === null) {
                                            isValidated = false
                                            choiceValidated = false
                                        }
                                    } else {
                                        if (answer[thenl['isSaveTo']] === null) {
                                            isValidated = false
                                            choiceValidated = false
                                        } else if (answer[thenl['isSaveTo']] === 'ใช่') {
                                            isValidated = false
                                            choiceValidated = false
                                        }
                                    }

                                })
                            }
                        } else if (ch.iptType === 'input') {
                            // console.log(answer[ch['isSaveTo']])
                            if (answer[ch['isSaveTo']] === null) {
                                isValidated = false
                                choiceValidated = false
                            } else if (answer[ch['isSaveTo']] === ' ') {
                                isValidated = false
                                choiceValidated = false
                            }
                        } else if (ch.iptType === 'checkbox') {
                            // console.log(answer[ch['isSaveTo']].length)
                            if (answer[ch['isSaveTo']].length === 0) {
                                isValidated = false
                                choiceValidated = false
                            }
                        }
                    } else {
                        isValidated = false
                        choiceValidated = false
                    }
                })
            }
            if (choiceValidated === false) {
                questions[idx]['isValidated'] = true
            } else {
                questions[idx]['isValidated'] = false
            }
            this.setState({questions})
        })
        return isValidated === true
    }

