    questionAbswer = (idx) => {
        let questionAbswer = ''
        const {questions} = this.state
        let ans = questions[idx]
        let i_ans = _.pickBy(_.identity(ans['answer']))
        if (ans.isMultiAnswer) {
            _.forEach(i_ans, (val, index) => {
                questionAbswer += `${val} ${index !== ans.length ? ',' : ''}`
            })
        } else {
            _.forEach(ans.choices, (q) => {
                const save = q.isSaveTo
                const a = i_ans[save]
                const delim = q.delim || null
                if (q.choice === a) {
                    questionAbswer += `${a} ${delim ? delim : ''}`

                    // console.log(questionAbswer)
                } else if (q.iptType === 'input') {
                    questionAbswer += `${a} ${delim ? delim : ''}`
                } else if (q.thens) {
                    _.forEach(q.thens, thenVal => {
                        if (i_ans[thenVal.isSaveTo] === thenVal.choice) {
                            questionAbswer += `${i_ans[thenVal.isSaveTo]} ${delim ? delim : ''}`
                        }
                    })
                } else if (q.iptType === 'option') {
                    questionAbswer += `${a} ${delim ? delim : ''}`
                }
                // else {
                //     // questionAbswer += `${a} ${delim ? delim : ''}`
                // }
            })

        }
        return questionAbswer
    }

    handleCustomerData = () => {
        const {savedData} = this.state
        let rowData = `,'${savedData['name'] + '  ' + savedData['lastname']}'`
        _.forEach(savedData, (value, key) => {
            if (key === 'region' || key === 'district' || key === 'province' || key === 'lastname' || key === 'modelpackage' || key === 'claimPackage' || key === 'name') {

            } else {
                rowData += `,'${value}'`
            }
        })
        return rowData
    }

    forceDisabled = (isNext = true) => {
        const {activeState, isScrollTop} = this.state
        let compareState = null
        if (isNext) {
            // console.log({STATE, activeState})
            const lastState = STATE[STATE.length - 1]
            compareState = lastState
            // console.log(compareState)
            if (activeState !== 'question' && !isScrollTop) {
                // console.log("innnnnnn")
                document.body.scrollTo = 0
                document.documentElement.scrollTop = 0
            }
        } else if (!isNext) {
            const firstState = STATE[0]
            compareState = firstState
        }
        return activeState === compareState
    }
