import React , {Component} from 'react'
import withMasterLayout from "../layouts/MasterLayout";
import {Alert} from 'react-bootstrap'
import ReactPixel from "react-facebook-pixel";

class KobKhub extends Component{
    componentDidMount() {
        ReactPixel.fbq('track', 'CompleteRegister')
    }

    render() {
        return (
            <div className={'row d-flex justify-content-center mt-5 mr-0'}>
                <div className={'col-8'}>
                    <Alert variant={'light'} className={'col-12 p-2'}>
                        <Alert.Heading className={'mt-2'}
                                       style={{color: 'green'}}>ขอบพระคุณที่ท่านให้ความสนใจ</Alert.Heading>
                        {
                            <div className={'p-4'}>
                                <p>สถานะ : รอการยืนยัน (BOOKED)</p>
                                <p>ขอบพระคุณที่ท่านร่วมลงทะเบียนจองเปลี่ยนถ่ายน้ำมันเครื่องกับศูนย์บริการคาสตรอล
                                    จะมีเจ้าหน้าที่ติดต่อไปเพื่อยืนยันการจอง</p>
                            </div>
                        }
                    </Alert>
                </div>

            </div>
        )
    }
}
export default withMasterLayout(KobKhub)